import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  decimalFormatter,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  percentualFormatter,
  referenceColumn,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, PagamentoCooperativa } from '../../../../models/dtos/mfp/pagamentoCooperativa';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.DESC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const PagamentoCooperativaSearchView: FC<SearchViewProps<PagamentoCooperativa>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    referenceColumn({
      dataField: 'referencia',
      editable: false,
      sort: true,
      text: 'Referência',
    }),
    {
      dataField: 'valor',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      formatter: decimalFormatter,
      sort: true,
      text: 'Valor',
    },
    {
      dataField: 'aliquota',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      formatter: percentualFormatter,
      sort: true,
      text: 'Alíquota',
    },
  ];

  return (
    <WindowComponent
      title="Manutenção de Pagamento à Cooperativa"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, '', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default PagamentoCooperativaSearchView;
